.header {
	$parent: &;

	position: relative;
	z-index: 10;
	padding: 27px 0;

	@include max(ti) {
		padding: 27px 0 12px;
	}

	&--tracks {
		position: absolute;
		top: 0;
		left: 0;
		display: none;
		width: 100%;
		padding: 21px 0;

		@include max(ti) {
			display: block;
		}

		#{$parent} {
			&__container {
				@include max(ti) {
					align-items: center;
				}
			}

			&__track {
				font-weight: 500;
				letter-spacing: 1.3px;

				@include max(ti) {
					font-size: fluid(14, 16, 576, 768);
				}

				@include max(t) {
					font-size: fluid(10, 14, 0, 576);
				}
			}

			&__burger {
				position: relative;
				width: 36px;
				height: 24px;

				span {
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					display: block;
					width: 100%;
					height: 2px;
					background-color: $color-text;

					&::before,
					&::after {
						content: "";
						position: absolute;
						display: block;
						width: 100%;
						height: 2px;
						background-color: $color-text;
						transition: 0.3s ease;
					}

					&::before {
						top: -10px;
					}

					&::after {
						bottom: -10px;
					}
				}
			}
		}
	}

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;

		@include max(ti) {
			align-items: flex-start;
		}
	}

	&__logo-orion {
		&:hover svg {
			opacity: 0.7;
		}

		svg {
			width: 60px;
			height: 40px;
			transition: 0.3s ease;

			@include max(ti) {
				width: 50px;
				height: 33px;
			}

			@include max(t) {
				width: 40px;
				height: 27px;
			}
		}
	}

	&__logo-cnews {
		display: inline-flex;

		&:hover svg {
			opacity: 0.7;
		}

		svg {
			width: 133px;
			height: 26px;
			transition: 0.3s ease;

			@include max(ti) {
				width: 100px;
				height: 20px;
			}

			@include max(t) {
				width: 62px;
				height: 12px;
			}
		}
	}
}
